import React, { useState, useRef, useEffect } from "react";
import { Col, Row, Container, Button } from "react-bootstrap";
import { navigate } from "gatsby";
import { TailSpin } from "react-loader-spinner";

import Layout from "../components/layout.js";
import val from "../components/validadores";
import Seo from "../components/seo";
import Keyboards from "../components/Keyboards.js";
import "../components/pages-css/sCita.css";
import VideoSeccion from "../components/videoSeccion.js";
import ModalEmergente from "../components/ModalEmergente.js";
import Timer from "../components/timer.js";
import api from "../components/actions/riveroapi";

export default function RealizaPago(){

    const [show, setShow] = useState(false);
    const [modal, setModal] = useState(false);
    const [errorInput, setErrorInput] = useState(false);
    const [error, setError] = useState(0);
    const [preferencia, setPreferencia] = useState("WhatsApp");
    const [click, setClick] = useState(false);
    const [loading, setLoading] = useState(false);
    const refForm = useRef();
    const [optionsKeyboard, setOptionsKeyboard] = useState({
      inpName: "",
      layoName: "phone",
      name: "",
      value: {}
    })
  
    function continuar(){
      //console.log("Pasa lo siguiente: ")

      var campo = optionsKeyboard.value.ReciboP;
      setLoading(true);
      api.getRealizaPago(campo).then((resp) => {
        //console.log(resp);

        if(resp.message === "Orden encontrada"){
            //console.log(resp.data[0].status)
            if(resp.data[0].status === "TERMINADA") {
                navigate("/postpago-tarjeta")
                localStorage.setItem("RutaNavegacion", "realizar-pago");
                setLoading(false);
                const params = {
                    orden: campo,
                    totalPago: resp.data[0].importe,
                }

                localStorage.setItem("ordenPago", JSON.stringify(params));
                ////console.log("Terminada la orden")
            } else {
                //console.log("la orden no está terminada")
                setModal(true)
                setError(11);
                setLoading(false);
            }

        } else {
            setModal(true)
            setError(11);
            setLoading(false);
        }

    });

    }

    return(
        <Layout backUrl={"/"}>
            <Seo title="Auto servicio" />
            {loading === true ? 
                <Container style={{position: 'absolute', zIndex: 100, height: '80%', display: 'flex', justifyContent: 'center', alignItems: 'center'}}>
                    <TailSpin
                    height="100"
                    width="100"
                    color="#0d6efd"
                    ariaLabel="tail-spin-loading"
                    radius="0"
                    wrapperStyle={{}}
                    wrapperClass=""
                    visible={true}
                    />
                </Container>
            : null}
            <Container className="container-sin-cita" onClick={() => {setClick(true)}}>
            {/*<Timer click={click} setClick={setClick} setError={setError} setModal={setModal}/>*/}
                <Row className="row-cita-arriba">
                    <div className="container-cita">
                        <Row>
                            <Col className="titulo-cita"><h1 className="titulo-h1">¿Qué tal?, Muy buen día.</h1></Col>
                        </Row>
                        <Row>
                            <Col className="titulo-cita" style={{flexDirection: 'column'}}>
                                <h4>Buscar tu recibo de pago</h4>
                                <p className="texto-gris">*¿Cómo quieres buscar tu recibo de pago?</p>
                            </Col>
                        </Row>
                        <Row style={{display: 'flex', justifyContent: 'center'}}>
                            <Row style={{display: 'flex', justifyContent: 'center', width: '70%'}}>
                                <Button className="btn-preferenciaActivo" id="btnWhatsApp">Orden de servicio</Button>
                            </Row>  
                        </Row>
    
                        <Row className="row-cita-abajo" style={{marginBottom: 0}}>
                            <Col lg={9}>
                                <input
                                autoComplete="off"
                                type="text"
                                placeholder="Ingresa tu número de orden"
                                id={errorInput === true ? "inputContactoError" : "inputContacto"}
                                className="form-control"
                                onClick={() => setShow(true)}
                                onFocus={() => setOptionsKeyboard({...optionsKeyboard, inpName: "ReciboP"})}
                                value={optionsKeyboard.value.ReciboP || ""}
                                />
                            </Col>
                        </Row>
                        <Row>
                            <Col className="titulo-cita" style={{flexDirection: 'column'}}>
                                <p className="texto-gris">- Ingresa el número de tu orden de servicio.</p>
                            </Col>
                        </Row>
                        <Button ref={refForm} onClick={() => continuar()} style={{display: 'none'}}></Button>
                        {show === true ? 
                            <center className="pt-5">
                            <Keyboards options={optionsKeyboard} setOptionsKeyboard={setOptionsKeyboard} optionsKeyboard={optionsKeyboard}/>
                            </center>
                        : null }
                    </div>
                </Row>
                <Row className="row-cita-abajo">
                    <VideoSeccion refForm={refForm} none="tiene"/>
                </Row>
            </Container>
            <ModalEmergente modal={modal} error={error} setModal={setModal} setClick={setClick}/>
        </Layout>
    )

}